ine-height: 16px;\n  text-transform: uppercase;\n}\n\n// Кнопки\n\n@mixin button {\n  font-style: normal;\n  font-weight: 500;\n}\n\n@mixin buttonGiant {\n  @include button;\n  font-size: 18px;\n  line-height: 24px;\n}\n\n@mixin buttonLarge {\n  @include button;\n  font-size: 16px;\n  line-height: 20px;\n}\n\n@mixin buttonMedium {\n  @include button;\n  font-size: 14px;\n  line-height: 16px;\n}\n\n@mixin buttonSmall {\n  @include button;\n  font-size: 12px;\n  line-height: 16px;\n}\n\n@mixin buttonTiny {\n  @include button;\n  font-size: 10px;\n  line-height: 12px;\n  letter-spacing: 1px;\n}\n\n// Прочее\n\n@mixin round($r) {\n  width: $r;\n  height: $r;\n  min-width: $r;\n  min-height: $r;\n  border-radius: 50%;\n}\n"],"sourceRoot":""}]);
